import React from 'react'
import { Card, CardImg, CardBody, CardTitle } from 'reactstrap';
import {Link} from 'react-router-dom'
import Config from '../config'

class MovieItem extends React.Component {


    render() {
        return(
            <Card className="entry-info">
                <CardImg top width="100%" src={`${Config.TMDB_POSTER_PATH}/w300_and_h450_bestv2${this.props.poster_path}`} alt="Card image cap" />
                <CardBody >
                    <CardTitle className="col-12">{this.props.title}</CardTitle>

                    <div className="row">
                        <div className="col-4">
                            <small className="text-muted">{this.props.Year}</small>
                        </div>
                        <div className="col-8">
                            <Link to={`/${this.props.id}`} className="btn btn-info btn-sm float-right">
                                info
                            </Link>

                        </div>
                    </div>
                </CardBody>
            </Card>
        )
    }
}

export default MovieItem



