import request from '../request'
import env from '../../env'

function search(term, type = null, year = null) {

    let query = '';

    // if IMDB movie id
    if(/^tt\d+$/.test(term)) {
        return this.getById(term);
    }

    return request({
        url: `/search/movie?api_key=${env.TMDB_API_KEY}&query=${term}`,
        method: 'GET'
    });
}

function getById(id) {
    return request({
        url: `/movie/${id}?api_key=${env.TMDB_API_KEY}&language=en-US`,
        method: 'GET'
    });
}

function getSerieEpisodes(imdb, season) {
    return request({
        url: `&i=${imdb}&Season=${season}`,
        method: 'GET'
    });
}


const tmdb = {
    search, getById, getSerieEpisodes
};

export default tmdb;


