import React from 'react'
import { Container, Row, Col } from 'reactstrap';
import {FormattedMessage } from 'react-intl';

import tmdb from '../services/api/tmdb';
import Header from './Header'
import Loader from './Loader'
import SeriesList from "./Pages/Series/SeriesList";
import Config from '../config'

class MovieInfo extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            id: this.props.match.params.id,
            result: {}
        };
    }

    componentWillReceiveProps(nextProps) {
        this.setState({
            id: nextProps.match.params.id,
            result: {}
        }, this.getMovieInfo);
    }

    componentDidMount() {
        this.getMovieInfo();
    }

    getMovieInfo() {
        document.title = 'loading';
        tmdb.getById(this.state.id)
            .then((response) => {
                this.setState({
                    result: response
                });
                document.title = response.Title;
            })
    }

    render() {

        return(
            <section>
                <Container>
                    <Header/>
                </Container>

                <Container fluid>


                    { Object.keys(this.state.result).length === 0 &&
                        <Row className='text-center'>
                            <Col>
                                <Loader/>
                            </Col>
                        </Row>
                    }

                    { Object.keys(this.state.result).length > 0 &&
                        <Row>
                            <Col>

                                <Row className='bg-primary'>
                                    <Col className='col-sm-10 offset-sm-1 col-md-8 offset-md-2'>
                                        <Row className="py-5">
                                            <Col className={'col-12'} >
                                                <div style={{'background-image': 'url(\'https://image.tmdb.org/t/p/w1400_and_h450_face/a9fRqsl6P27qxbDSb4pBXNv9Fwl.jpg\')'}}></div>
                                            </Col>
                                            <Col className="col-4">
                                                <img className="img-thumbnail" src={`${Config.TMDB_POSTER_PATH}/w1280/${this.state.result.poster_path}`} alt="poster"/>
                                            </Col>
                                            <Col className="col-8">
                                                <Row>
                                                    <Col>
                                                        <h2>{this.state.result.title}</h2>
                                                        <p>{this.state.result.release_date}</p>
                                                        <p>{this.state.result.runtime}</p>
                                                        <p>{this.state.result.overview}</p>
                                                    </Col>
                                                </Row>

                                                <Row>
                                                    <Col>
                                                        <dl className="row">
                                                            <dt className="col-sm-3">
                                                                <FormattedMessage id="movieInfo.director" />
                                                            </dt>
                                                            <dt className="col-sm-9">{this.state.result.Director}</dt>

                                                            <dt className="col-sm-3">
                                                                <FormattedMessage id="movieInfo.writer" />
                                                            </dt>
                                                            <dt className="col-sm-9">{this.state.result.Writer}</dt>

                                                            <dt className="col-sm-3">
                                                                <FormattedMessage id="movieInfo.genere" />
                                                            </dt>
                                                            <dt className="col-sm-9">{this.state.result.Genere}</dt>

                                                            <dt className="col-sm-3">
                                                                <FormattedMessage id="movieInfo.production" />
                                                            </dt>
                                                            <dt className="col-sm-9">{this.state.result.Production}</dt>

                                                            <dt className="col-sm-3">
                                                                <FormattedMessage id="movieInfo.released" />
                                                            </dt>
                                                            <dt className="col-sm-9">{this.state.result.Released}</dt>

                                                        </dl>
                                                    </Col>
                                                </Row>
                                            </Col>
                                        </Row>
                                    </Col>
                                </Row>

                            <Row className="px-4">
                                <Col className={this.state.result.Type === 'series' ? 'col-6' : 'col-12'}>
                                    <Row>
                                        <Col>
                                            <h4>
                                                <FormattedMessage id="movieInfo.actors" />
                                            </h4>
                                            <p>{this.state.result.Actors}</p>
                                        </Col>
                                    </Row>

                                    <Row>
                                        <Col>
                                            <h4>
                                                <FormattedMessage id="movieInfo.awards" />
                                            </h4>
                                            <p>{this.state.result.Awards}</p>
                                        </Col>
                                    </Row>

                                    {this.state.result.BoxOffice &&
                                    <Row>
                                        <Col>
                                            <h4>Box Office</h4>
                                            <p>{this.state.result.BoxOffice}</p>
                                        </Col>
                                    </Row>
                                    }
                                </Col>

                                {this.state.result.Type === 'series' &&
                                    <Col className='col-6'>
                                        <SeriesList id={this.state.result.id} totalSeasons={this.state.result.totalSeasons} />
                                    </Col>
                                }


                            </Row>




                            </Col>
                        </Row>
                    }

                </Container>


            </section>
        )
    }
}

export default MovieInfo



